<template>
	<div>
  <div class="page-title">
    <h3>Планирование</h3>
    <h4>{{info.bill | currencyFilter}}</h4> <!-- пропускауем значение через currencyFilter, зареганный глобально в main.js -->
  </div>
  <Loader v-if="loading"></Loader>
  <p v-else-if="!categories.length">Категорий пока нет <router-link to="/categories">Добавить новую категорию</router-link></p>
  <section v-else>
    <div v-for="cat in categories" :key="cat.id">
      <p>
        <strong>{{cat.title}}:</strong>
        {{cat.spend | currencyFilter}} из {{cat.limit | currencyFilter}}
      </p>
      <div class="progress" v-tooltip="cat.tooltip"> <!-- добавление директивы tooltip -->
        <div
            class="determinate"
            :style="{width: cat.progressPersent + '%', backgroundColor: cat.progressColor}"
        ></div>
      </div>
    </div>
  </section>
</div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import currencyFilter from '@/filters/currency.filter' //мпорт фильтра для использования не в шаблоне компонента, а в коде (в теге script)
  import localizeFilter from '@/filters/localize.filter'
  export default {
    name: 'planning',
    data(){
      return{
        loading: true,
        categories: []
        
      }
    },
    computed: mapGetters(['info']),
    async mounted(){
      const records = await this.$store.dispatch('fetchRecords')
      const categories = await this.$store.dispatch('getCategories')

      this.categories = categories.map(cat => {
        const spend = records
          .filter(r => r.categoryId === cat.id && r.type === "outcome")
          // .filter(r => r.type === "outcome")
          .reduce((total, record) => {
            return total += +record.amount
          }, 0)
          
          const percent = 100 * spend / cat.limit
          const progressPersent = percent > 100 ? 100 : percent
          const progressColor = percent < 60
            ? 'green'
            :percent < 100
              ? 'yellow'
              : 'red'

          const tooltipValue = cat.limit - spend
          const tooltip = tooltipValue < 0 ? `${localizeFilter('tooltipLimitOver')} ${currencyFilter(Math.abs(tooltipValue))}`
            : `${localizeFilter('tooltipLimitLeft')} ${currencyFilter(tooltipValue)}`//использование фильтра не в хтмл-шаблоне, а в коде

          return {
            ...cat,
            spend,
            progressPersent,
            progressColor,
            tooltip
          }
      })

      // const filteredCats = categories.map((cat) => {
      //   const ircom =  records.filter((record) =>{

      //     return record.categoryId === cat.id && record.type === "outcome"
      //   }).reduce((start, rec) => {
      //      // console.log(record)
      //     return start + rec.amount}, 0)
      //   return {
      //     ...cat,
      //     ircom
      //   }
      // })
      
     
      console.log(records)
      console.log(categories)
      // console.log(this.categories )
      // console.log( filteredCats)
      this.loading = false
    }
  }
</script>